import Vue from 'vue'
import Router from 'vue-router'
import List from './List.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/project',
      name: '项目列表',
      component: List
    }, 
    { path: '*', redirect: '/project' }
  ]
})
